import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Range } from 'react-range';
import '../register/Register.css'; 
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';

const apiUrl = `https://civil-5pwk.onrender.com`
const Register = ({addUser ,setAddUser}) => {
  const [formdata, setFormdata] = useState({
    desc: '',
    name: '',
    price:null,
    image:""
  });
const [image , setImage] = useState("");


  const navigate = useNavigate()
  function handlefileChange(e) {
    
    const {name , value} = e.target
      const reader = new FileReader();
      reader.readAsDataURL(e.target.files[0]);
      reader.onload = () => {
        const dataURL = reader.result;
       
        console.log('dataURL:', dataURL);
  
        setFormdata((prevdata) => ({
          ...prevdata,
          [name]: dataURL,
        }));
      };
  
    reader.onerror = error =>{
      console.log('Error:', error)
    }
  } 
  

  
  const handleChange = (e) => {
    const { name, value } = e.target;
console.log('image_39' ,image)
    setFormdata((prevdata) => ({
      ...prevdata,
      [name]: value,
    }));
  };
 
  const handleSubmit = async () => {
    console.log('formdata:62' , formdata)
    const response = await axios.post(`/api/v1/admin/createCourse` , formdata)
  
    console.log("resposne:73",response)
    navigate('/')
  }; 

  return (
    <div className="register_container">
      
      <div className='form_container'>
      <Form className="selected-value">
      <div className='input_container'>
      <h2 className='close' onClick={()=>navigate('/')}>x</h2>
      <h2>Add Product</h2>
        
      </div>
 
      <div className='input_container'>
        <Form.Group>
          <Form.Label>Name</Form.Label>
          <Form.Control
            type="text"
            placeholder="Name"
            name="name"
            onChange={handleChange}
            required
          />
        </Form.Group>
      </div>
      <div className='input_container'>
        <Form.Group>
          <Form.Label>Description</Form.Label>
          <Form.Control
            type="text"
            placeholder="Description"
            name="desc"
            onChange={handleChange}
            required
          />
        </Form.Group>
      </div>

      <div className='input_container'>
        <Form.Group>
          <Form.Label>Price</Form.Label>
          <Form.Control
            type="number"
            placeholder="Price"
            name="price"
            onChange={handleChange}
            required
          />
        </Form.Group>
      </div>

      <div id='input_containerr' className='input_container'>
        <Form.Group>
          <Form.Label>Image</Form.Label>
          <input
          className='inputfile'
          accept='image/*'
            type="file"
            placeholder="Image"
            name="image"
            onChange={handlefileChange}
            required
          />
        </Form.Group> 
          </div>

<div className='input_container'>
        <Button variant="success" onClick={handleSubmit}>
          Submit
        </Button>
    
  </div>
      </Form>
      </div>
    </div>
  );
};

export default Register;
