import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Range } from 'react-range';
import './Register.css';
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';

const apiUrl = `https://civil-5pwk.onrender.com`
const Register = ({addUser ,setAddUser}) => {
  const [formdata, setFormdata] = useState({
    title: 'Mr',
    name: '',
    email: '',
    password: '',
    image:""
  });
const [image , setImage] = useState("");
  const [values, setValues] = useState([50]);

  const navigate = useNavigate()
  function handlefileChange(e) {
    
    const {name , value} = e.target
      const reader = new FileReader();
      reader.readAsDataURL(e.target.files[0]);
      reader.onload = () => {
        const dataURL = reader.result;
       
        console.log('dataURL:', dataURL);
  
        setFormdata((prevdata) => ({
          ...prevdata,
          [name]: dataURL,
        }));
      };
  
    reader.onerror = error =>{
      console.log('Error:', error)
    }
  } 
  

  
  const handleChange = (e) => {
    const { name, value } = e.target;
console.log('image_39' ,image)
    setFormdata((prevdata) => ({
      ...prevdata,
      [name]: value,
    }));
  };
 
  const handleSubmit = async () => {
    console.log('formdata:62' , formdata)
    const response = await axios.post(`/api/v1/register` , formdata)
  
    if (response.data.success === true) {
      // Accessing user data
      const userData = response.data.user;
      
      // Accessing token
      const token = response.data.token;
      // const options = response.data.options;
      
      // Convert user data and token to strings
      const userDataString = JSON.stringify(userData);
      const tokenString = JSON.stringify(token);
  
      // Save user data and token to localStorage
      localStorage.setItem('userData', userDataString);
      localStorage.setItem('token', tokenString);
      // const expirationDate = new Date(options.expires);

      // document.cookie = `token=${token}; expires=${expirationDate.toUTCString()}; path=/`;
      
      localStorage.setItem('islogin', true);
      navigate('/')
  } else {
      console.log("error:" ,response)
  }
  
    console.log("resposne:73",response)
  }; 

  return (
    <div className="register_container">
      
      <div className='form_container'>
      <Form className="selected-value">
      <div className='input_container'>
      <h2 className='close' onClick={()=>navigate('/')}>x</h2>
      <h2>Register</h2>
        <Form.Group>
          <Form.Label>Title</Form.Label>
          <Form.Control
            as="select"
            name="title"
            onChange={handleChange}
            required
          >
            <option value="Mr">Mr</option>
            <option value="Mrs">Mrs</option>
            <option value="Miss">Miss</option>
          </Form.Control>
        </Form.Group>
      </div>
 
      <div className='input_container'>
        <Form.Group>
          <Form.Label>Name</Form.Label>
          <Form.Control
            type="text"
            placeholder="Name"
            name="name"
            onChange={handleChange}
            required
          />
        </Form.Group>
      </div>
      <div className='input_container'>
        <Form.Group>
          <Form.Label>Email</Form.Label>
          <Form.Control
            type="email"
            placeholder="Email"
            name="email"
            onChange={handleChange}
            required
          />
        </Form.Group>
      </div>

      <div className='input_container'>
        <Form.Group>
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            placeholder="Password"
            name="password"
            onChange={handleChange}
            required
          />
        </Form.Group>
      </div>

      <div id='input_containerr' className='input_container'>
        <Form.Group>
          <Form.Label>Image</Form.Label>
          <input
          className='inputfile'
          accept='image/*'
            type="file"
            placeholder="Image"
            name="image"
            onChange={handlefileChange}
            required
          />
        </Form.Group> 
          </div>

        


<div className='input_container'>
        <Button variant="success" onClick={handleSubmit}>
          Submit
        </Button>

        <Link to="/login"  style={{color:'#fff'}}>Already a user? Sign in here</Link>
    
  </div>
      </Form>
      </div>
    </div>
  );
};

export default Register;
