import React,{useState} from 'react';
import { styled, alpha } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import Badge from '@mui/material/Badge';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import AccountCircle from '@mui/icons-material/AccountCircle';
import MailIcon from '@mui/icons-material/Mail';
import NotificationsIcon from '@mui/icons-material/Notifications';
import MoreIcon from '@mui/icons-material/MoreVert';
import Register from '../register/Register';
import AddIcon from '@mui/icons-material/Add';
import { Navigate, useNavigate } from 'react-router-dom';
 import icon from '../SendNotification/Design1.jfif'
import './Navbar.css'
import axios from 'axios';
import NotificationCard from '../Notification/NotificationCard';


export default function PrimarySearchAppBar() {
  const [showNotification, setShowNotification] = useState(false)
  const [addUser , setAddUser] = React.useState(false)
  const[allNotification , setAllNotification] = React.useState([])
  const [notificationData , setNotificationData] = useState([])

  React.useEffect(() => {
    // Function to run when component mounts 
    const fetchNotification = async () => {
      try {
       
      const {data} =  await axios.get(`/api/v1/getNotification`);
        console.log('API called successfully_37', data,setNotificationData(data.data));  
        
      } catch (error) {
        console.error('Error calling API:', error);
      }
    };
   
     fetchNotification()
  }, []); 

  const menuId = 'primary-search-account-menu';

  const navigate = useNavigate()
  const userDataString = localStorage.getItem('userData');
  const userData = JSON.parse(userDataString);
  const userRole = userData?.role || "";

  
  const notiShowHandler = () => {
    setShowNotification(!showNotification);
  };
console.log('notification:' , notificationData)
  return (
    <Box style={{ width: "100%" }}>
      {addUser && <Register addUser={addUser} setAddUser={setAddUser}/>}

      <AppBar position="static" style={{ background: "transparent" , color:"black" }}>
        <Toolbar>
          <IconButton
            size="large" 
            edge="start"
            color="inherit"
            aria-label="open drawer"
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{ display: {  sm: 'block' } }}
          > <img className='avataar' src={icon}/>
           
          </Typography>
          
          <Box sx={{ flexGrow: 1 }} />
          <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
            <IconButton size="large" aria-label="show 4 new mails" color="inherit">
              <Badge badgeContent={4} color="error">
                <MailIcon />
              </Badge>
            </IconButton>
           <IconButton
          size="large"
          aria-label="show 17 new notifications"
          color="inherit"
          onClick={notiShowHandler} // Corrected onClick attribute
        >
          <Badge badgeContent={notificationData?.length || 4} color="error">
            <NotificationsIcon />
          </Badge>
        </IconButton>
        {showNotification && (
  <IconButton className='iconbuton'>
    <div className='notification_container'>{notificationData.map((notification) => (
      <div className="notification-card" key={notification._id}>
        <p>{notification.title}</p>
        <p>{notification.message}</p>
      </div>
    ))}
    </div>
  </IconButton>
)}

          {userRole == "admin" &&  
          <IconButton
              size="large"
              edge="end"
              aria-label="add product"
              aria-haspopup="true"
              onClick={()=>navigate('/createProduct')} 
              color="inherit"
            >
              <AddIcon />
            </IconButton>
            }
            <IconButton
              size="large"
              edge="end"
              aria-label="account of current user"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={()=>navigate('/register')} 
              color="inherit"
            >
             {userData ? <img className='avataar' src={userData.image}/> : <AccountCircle />}
            </IconButton>
          </Box>
        </Toolbar>
      </AppBar>
    </Box>
  );
}
