import React from 'react'
import CheckoutCourses from '../Checkout/CheckoutCourses'
import './Checkout.css'
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { Link, useNavigate } from 'react-router-dom';
 import '../Checkout/Checkout.css'
 import axios from 'axios';


const Checkout = () => {
    const navigate = useNavigate()

    const handleStatus =async() =>{
     
        try {
          const dataToSend = {
            checkoutPage: true,
            paid: true
          };
      
          
          const response = await axios.put('/api/v1/updateUserStatus', dataToSend);
          console.log('Data sent successfully:', response.data);
          navigate('/success')
        } catch (error) {
          console.error('Error sending data:', error);
        }
      
    }
  return (
    <div className='checkoutCover'>
    <div className='bgblur'>
        <p className='button2' onClick={()=>navigate('/')}>x</p>
    <div className='checkoutContainer'>
        <div className='coursesContainer'>
        <Card sx={{ maxWidth: 345  }}>
      <CardMedia 
        sx={{ height: 140 }}
        image="https://www.teachengineering.org/Images/edpHub/EDPHub_Graphic.png"
        title="green iguana"
      />
      <CardContent>
        <Typography gutterBottom variant="h5" component="div">
          Ed - (Part-1)
        </Typography>
        <Typography variant="body2" color="text.secondary">
        This engineering design involves the creation of a suspension bridge, a type of bridge where the roadway deck is suspended from vertical cables anchored to two tall towers.
        </Typography>
        <Typography style={{color:'orange' , fontWeight:600}} gutterBottom variant="h6" component="div">
          INR - 4000 /-
        </Typography>
      </CardContent>
      <CardActions>
        </CardActions>
    </Card>
        </div>

        <div class="checkout-container">
        <h2>Final Checkout</h2>
        <table class="checkout-table">
            <tr>
                <th>Description</th>
                <th>Price</th>
            </tr>
            <tr>
                <td>Original Price</td>
                <td>$100</td>
            </tr>
            <tr>
                <td>Tax (GST)</td>
                <td>$15</td>
            </tr>
            <tr>
                <td><strong>Final Price</strong></td>
                <td><strong>$115</strong></td>
            </tr>
        </table>
        <p>Review your items and proceed to payment.</p>
        <button onClick={handleStatus}  class="proceed-button">Proceed to Payment</button>
    </div>
    </div>
    </div>
    </div>
  )
}

export default Checkout