import React, { useEffect } from 'react';
import logo from '../SendNotification/Design1.jfif'
function SendNotification({ showNotification, setShowNotification, message }) {
  
  useEffect(() => {
    // Function to show notification when component mounts
    const showNotificationAutomatically = () => {
      // Check if showNotification is true
      if (showNotification) {
        // Request permission for browser notifications
        Notification.requestPermission().then((permission) => {
          if (permission === 'granted') {
            // Show the notification
            showNotificationFunc('Civil-GuruJi', message , logo);
            setShowNotification(false)
          }
        });
      }
    };

    // Call the function to show notification automatically
    showNotificationAutomatically();
  }, [showNotification, message]); // Run effect when showNotification or message changes

  const showNotificationFunc = (title, body ,icon) => {
    // Check if the browser supports notifications
    if (!('Notification' in window)) {
      console.log('This browser does not support system notifications');
      return;
    }

    // Check if permission has been granted for notifications
    if (Notification.permission === 'granted') {
      // Show the notification
      new Notification(title, { body ,icon});
      // Update state to prevent showing the notification again
      setShowNotification(false);
    }
  };

  return null; // Return null to prevent rendering any content
}

export default SendNotification;
