import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import './Courses.css';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

export default function MediaCard() {
  const navigate = useNavigate();
  const [courses, setCourses] = React.useState([]);

  React.useEffect(() => {
    const fetchAllCourses = async () => {
      try {
        const response = await axios.get('/api/v1/getAllCourse');
        console.log('response_course:', response.data.data);
        setCourses(response.data.data);
      } catch (error) {
        console.log("error:", error);
      }
    };
    fetchAllCourses();
  }, []);

  const handleStatus = async() => {
    try {
      const dataToSend = {
        checkoutPage: true,
        paid: false
      };
  
      
      const response = await axios.put('/api/v1/updateUserStatus', dataToSend);
      console.log('Data sent successfully:', response.data);
      navigate('/Checkout')
    } catch (error) {
      console.error('Error sending data:', error);
    }
  };

  return (
    <div className='coursesContainer'>
      {courses.map((data) => (
        <div className='coursesBox' key={data.id}>
          <Card sx={{ maxWidth: 345 }}>
            <CardMedia
              sx={{ height: 140 }}
              image={data.image}
              title="Course Image"
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="div">
                {data.name}
              </Typography>
              <Typography variant="body2" color="text.secondary" height={100}>
                {data.desc}
              </Typography>
              <Typography style={{ color: 'orange', fontWeight: 600 }} gutterBottom variant="h6" component="div">
                INR - {data.price} /-
              </Typography>
            </CardContent>
            <CardActions>
              <Button className='button1' onClick={() => handleStatus(data.id)}>Buy Now</Button>
            </CardActions>
          </Card>
        </div>
      ))}
    </div>
  );
}



