import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Range } from 'react-range';
import '../register/Register.css';
import axios from 'axios';
import { useNavigate, useParams } from 'react-router-dom';

const Update = ({updateUser ,setUpdateUser}) => {
    const params = useParams()
    const navigate = useNavigate()

  const [formdata, setFormdata] = useState({
    
    name: '',
    age:0,
    dob: new Date(),
    image:""
  });
const [image , setImage] = useState("");
  const [values, setValues] = useState([50]);

  
  function handlefileChange(e) {
    const {name , value} = e.target
      const reader = new FileReader();
      reader.readAsDataURL(e.target.files[0]);
      reader.onload = () => {
        const dataURL = reader.result;
       
        console.log('dataURL:', dataURL);
  
        setFormdata((prevdata) => ({
          ...prevdata,
          [name]: dataURL,
        }));
      };
  
    reader.onerror = error =>{
      console.log('Error:', error)
    }
  }
  
  

   
  const handleChange = (e) => {
    const { name, value } = e.target;
console.log('image_39' ,image)
    setFormdata((prevdata) => ({
      ...prevdata,
      [name]: value,
    }));
  };

  const handleRangeChange = (newValues) => {
const value = newValues[0].toFixed(0)
    setValues(newValues); 
   
    setFormdata((prevdata) =>({
        ...prevdata,
        ["age"]:value
    }))
  };

  const handleDateChange = (date) => {
    setFormdata((prevdata) => ({
      ...prevdata,
      dob: date,
    }));
  };
 
  const handleSubmit = async (e) => {
    console.log('formdata:62' ,e,"formdata:" ,formdata)
    const resposne = await axios.put(`/api/v1/update/${params.id}` , formdata)
    navigate('/')
  };

  const handleNavigate = () => {
    console.log('navigate')
    navigate('/');
  };
  return (
    <div className="register_container">
      
      <div className='form_container'>
      <Form className="selected-value">
      <div className='input_container'>
      <h1 className='close' onClick={handleNavigate}>x</h1>
      <h1>Update</h1>
       
      </div>
 
      <div className='input_container'>
        <Form.Group>
          <Form.Label>Name</Form.Label>
          <Form.Control
            type="text"
            placeholder="Name"
            name="name"
            onChange={handleChange}
            required
          />
        </Form.Group>
      </div>
      
      <div className='input_container'>
        <Form.Group>
          <Form.Label>Image</Form.Label>
          <Form.Control
          accept='image/*'
            type="file"
            placeholder="Image"
            name="image"
            onChange={handlefileChange}
            required
          />
        </Form.Group> 
      </div>

        <div className='input_container'>
        <Form.Group>
          <Form.Label>Date of Birth</Form.Label>          
        </Form.Group>

        <DatePicker
            className="form-control"
            selected={formdata.dob}
            onChange={handleDateChange}
            dateFormat="MM/dd/yyyy"
            placeholderText="DOB"
          />
</div>

<div className='input_container'>
<Form.Group>
          <Form.Label>Age</Form.Label>          
        </Form.Group>
        <div className="custom-slider">
        <Form.Group>
          <Range
            values={values}
            step={0.01}
            min={0}
            max={100}
            onChange={handleRangeChange}
            renderTrack={({ props, children }) => (
              <div
                {...props}
                style={{
                  ...props.style,
                  height: '6px',
                  width: '100%',
                  borderRadius: '3px',
                  background: '#d8d8d8',
                }}
              >
                {children}
              </div>
            )}
            renderThumb={({ props }) => (
              <div
                {...props}
                style={{
                  ...props.style,
                  height: '16px',
                  width: '16px',
                  backgroundColor: '#007bff',
                  borderRadius: '50%',
                  border: '2px solid #007bff',
                  cursor: 'pointer',
                }}
              />
            )}
          />
        </Form.Group>
          <div
            className="range-label"
            style={{
              position: 'relative',
              textAlign:'center', 
              top: '-50px',              
              width:'50px',
              left: `${(values[0] / 100) * 100}%`,
              transform: 'translateX(-50%)',
              padding: '5px',
              background: '#007bff',
              color: '#fff',
              borderRadius: '5px',
            }}
          >
            {values[0].toFixed(0)}
          </div>
        </div>
</div>
<div className='input_container'>
        <Button variant="success" onClick={handleSubmit}>
          Update
        </Button>
  </div>
      </Form>
      </div>
    </div>
  );
};

export default Update;
